#root,html,body{
    margin:0;
    padding: 0;
    height: 100vh;
    box-sizing: border-box;
    font-family: 'Spartan', sans-serif;
}

.app{
    height:100%;
    background: linear-gradient(rgba(0,0,0,0.3),rgba(0,0,0,0.3) ), url("https://raw.githubusercontent.com/adrianhajdin/advice_app/master/src/images/city.jpg");
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-left: 10%;
    padding-right: 10%;
}

.card{
    background-color: whitesmoke;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 25px;
    padding: 10%;
    box-shadow: 10px 10px;
}


.button {
  background-image: linear-gradient(92.88deg, #455EB5 9.16%, #5643CC 43.89%, #673FD7 64.72%);
  border-radius: 8px;
  border-style: none;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  flex-shrink: 0;
  font-family: "Inter UI","SF Pro Display",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;
  font-size: 16px;
  font-weight: 500;
  height: 3.2rem;
  padding: 0 1.6rem;
  text-align: center;
  text-shadow: rgba(0, 0, 0, 0.25) 0 3px 8px;
  transition: all .5s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button:hover {
  box-shadow: rgba(80, 63, 205, 0.5) 0 1px 30px;
  transition-duration: .1s;
}

.auther{
    padding-bottom: 10px;
    margin-top:30px;
    margin-bottom: -48px;
}

.auther a {
    text-decoration: none;
    background-color:#f8f8f8;
    padding:5px;
    border-radius: 8px;
    border-style: none;
    box-sizing: border-box;
    
}
